<template>
    <div class="mw-page page approve" v-nav="{hideNavbar:true}">
        <div class="mw-page-header">
            <i class="left icon ion-ios-arrow-back" @click="$router.go(-1)"></i>
            <span>审批意见</span>
            <i class="right"></i>
        </div>
        <div class="mw-page-content">
          <item>
            <span>状态 ： {{}}</span>
          </item>
        </div>
    </div>
</template>

<script>
  import { submitApprove,approveSuggests } from '@/api/approve'
    export default{
      name:'approveSuggests',
        data(){
          return {
            bill_id:'',
            bill_type:'',
            suggests:[

            ]
          }
        },
      mounted(){
        this.bill_id = this.$route.params.bill_id
        this.bill_type = this.$route.params.bill_type

        approveSuggests({
          bill_id:this.bill_id,
          bill_type:this.bill_type
        }).then(res=>{
          console.log(res)
          this.suggests = res.process_list
        }).catch(error=>{
          $toast.show(error.description)
        })
      },
    }
</script>

<style lang="scss" scoped>
  textarea{
    width: 100%;
    height: 80%;
    resize: none;
    padding: 15px;
  }
</style>



